<template>
  <div class="section">
    <div class="content review-sign">
      <div class="text">
        <div class="normal">
          <p>你本年的流程审批有<span class="red-bold ml5">{{info.flowSpNum}}</span>条</p>
          <p>平均耗时<span class="red-bold ml5">{{info.avgTime.hour}}</span>小时</p>
          <p>击败 <span class="orange-bold bigger">{{info.ratio}}</span>审批人</p>
<!--          <p v-if="info.startTime">最快的审核<span class="red"><span class="m5 bold">{{info.startTime.hour}}</span>小时</span></p>-->
        </div>
        <div class="ease">
          <p>
            <span class="red">{{info.latestDate}}，</span>
            <span v-if="before">继续奋斗的一天</span>
            <span v-else>继续奋斗的夜晚，</span>
          </p>
          <p v-if="info.latestTime">
            <span class="red bold mr5">{{info.latestTime}}</span>
            <span v-if="before">在流程审核中，</span>
            <span v-else>的你还在审核流程，</span>
          </p>
          <p>幸运如我，感恩有你，</p>
          <p><span v-if="before">美好金科里</span><span v-else>深夜的烟火</span>有属于你的一笔色彩</p>
        </div>
      </div>
      <div class="tag"><p class="tag-text">{{info.badgeType.value}}</p></div>
      <div class="cloud">
        <img src="@/assets/cloud1.png" class="cloud1">
        <img src="@/assets/cloud2.png" class="cloud2">
        <img src="@/assets/cloud3.png" class="cloud3">
      </div>
      <div class="image">
        <img src="@/assets/sign/img1.png" class="img1">
        <img src="@/assets/sign/img2.png" class="img2">
        <div class="line"></div>
      </div>
      <div class="prompt">
        <p>想看你的年底总结吗？</p>
        <p>美好金科APP-消息-搜索“温暖同行”</p>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      info: {
        default: () => {}
      }
    },
    computed: {
      before() {
        let hour = this.info.latestTime.split(':')[0]
        if (hour < 21 && hour > 5) {
          return true
        } else {
          return false
        }
      }
    },
  }
</script>

<style scoped lang="less">
  .review-sign {
    background: url('~@/assets/sign/bg.png') center e('/') cover;
    .text {
      top: 17.5%;
    }
    //动画
    @keyframes show1 {
      0% {
        transform: scale(0);
      }
      100% {
        transform: scale(1);
      }
    }
    @keyframes show2 {
      0% {
        width: 0;
        height: 0;
        left: 205px;
        opacity: 1;
      }
      50% {
        width: 170px;
        height: 0;
        left: 35px;
        opacity: 1;
      }
      100% {
        width: 170px;
        height: 65px;
        left: 35px;
        opacity: 1;
      }
    }
    .img1 {
      width: 180px;
      position: absolute;
      right: -55px;
      top: 0;
      transform: scale(0);
      .active & {
        animation: show1 1s forwards;
      }
    }
    .img2 {
      width: 126px;
      position: absolute;
      bottom: 20px;
      left: -35px;
      transform: scale(0);
      .active & {
        animation: show1 1s 2s forwards;
      }
    }
    .line {
      position: absolute;
      top: 90px;
      border-top: 1px solid #ff9494;
      border-left: 1px solid #ff9494;
      opacity: 0;
      .active & {
        animation: show2 1s 1s forwards;
      }
    }
  }
</style>